@font-face {
  font-family: rota-bold;
  src: url(../public/res/Rota-Bold.otf);
}

@font-face {
  font-family: rota-semi;
  src: url(../public/res/Rota-SemiBold.otf);
}

@font-face {
  font-family: rota-black;
  src: url(../public/res/Rota-Black.otf);
}
:root {
  --ey : '#F9B61F';
  --ep :'#4F3084'
}
/* Colors */
/* purple 4F3084 */
/* yellow F9B61F */


body {
  margin: 0;
  font-family: rota-semi;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.user-login, .otp-container{
  background-color: #F9B61F ;
  height: 100vh;
}
/*  -------------- Purple Button ------------------- */

button.MuiButton-root {
  border-radius: 32px;
}

button.p-btn {
  color: white;
  background-color: #4F3084;
  text-transform: none;
  /* margin-top: 1.5em; */
  font-family: rota-bold;
  /* min-width: 120px; */
  /* width: 80%;
  max-width: 600px; */

}

button.p-btn:disabled{
  color: white;
  background-color: #5d5270;

}

 button.p-btn:hover {
  color: white;
  background-color: #4F3084;

}

button.p-btn:focus {
  background-color: #4F3084;
  color: white;
}

/* ---------- Yellow button -------------------- */
button.y-btn {
  color: #4F3084;
  background-color: #F9B61F;
  text-transform: none;
  /* margin-top: 1.5em; */
  font-family: rota-bold;
  /* min-width: 120px; */
  /* width: 80%;
  max-width: 600px; */

}

button.y-btn:disabled{
  color: #4F3084;
  background-color: #F9B61F;

}

 button.y-btn:hover {
  color: #4F3084;
  background-color: #F9B61F;

}

button.y-btn:focus {
  background-color: #F9B61F;
  color: #4F3084;
}



 label.MuiFormLabel-root  {
  font-family: rota-semi;

}


/* --------------- OTP -------------- */


.otp-text {
  margin-bottom: 1em;
  color: #4F3084;
  text-align: center;
}
.otpText{
  color:red;
}
.otp-container input {
  padding: 0.5em;
  font-size: large;
  border-radius: 4px;
  margin: 1em 0.5em;
  border: none;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
}
.otp-container.wrong input {

color:red;
}

.otp-resend{
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

/* ---------------- Home ---------------------- */

.home .count{
  line-height: 1;
  color:#4F3084;
  margin: 0;
}

/* ---------------- Coupon --------------- */

.couponFull  .MuiDialog-container > div{
  background-color: transparent;
  box-shadow: none;
}

.couponFull  .MuiDialog-container > div > div{
  padding:0;
}

/* ------------------- Coupon Terms -------------- */
.terms li p, .terms li {
  margin:0;
  margin-top: 1px;
  font-size: 10px;
}

.terms li p:empty {
  display: none;
}


.terms ul {
  padding-inline-start:20px;
}